import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import {Constants} from '../config/config';
 
@Injectable()
export class UserServices{

    signupURL;
    
    constructor(private http:HttpClient){
     
    }

    getUsersUrls(){
      return  this.http.get(Constants.API_ENDPOINT_users).subscribe((res)=>{
          const arr=<Array<String>>res;
           for(let i=0;i<arr.length;i++){
               if(res[i]!==undefined){
                if(res[i].rel==Constants.signup){
                    this.signupURL=res[i].href;
                }
               }
                
            }
        })
    }

    signup(user){
        //let headers=new HttpHeaders();
         return this.http.post(this.signupURL,user);
    }

    login(loginDetails){
        let loginURL=Constants.API_ENDPOINT+"/login?username="+loginDetails.username+"&password="+loginDetails.password;
      
        return this.http.post(loginURL,{},{responseType: 'text', observe: 'response'});


        
    }

}