import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import { UserServices } from 'src/app/services/user.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
signupForm:FormGroup;
  constructor(private fb:FormBuilder,private userServices:UserServices,private flashMessage:FlashMessagesService) { 
    this.signupForm=fb.group({
      'fname':[],
      'lname':[],
      'aadharID':[],
      'phone':[],
      'password':[],
      'email':[]


    })
  }

  ngOnInit() {
  }


  onSubmit(){
    let result=this.userServices.signup(this.signupForm.value).subscribe((res)=>{
      this.flashMessage.show(res.toString());   
      
   });
  }
}
